import React from 'react';
import './Body.css';
import Sidebar from './Sidebar';
import Rightbar from './Rightbar';


function Body() {
    return (
        <div className="body">

            <div className="sidebar">
                <Sidebar/>
            </div>
            <div className="rightbar">
                <h1><Rightbar/></h1>
            </div>
            
        </div>
    )
}

export default Body
