import React from 'react';
import './Navbar.css';
import Resume from './Resume';
import Body from './Body';
import {Link} from 'react-router-dom';
import GitHubIcon from '@material-ui/icons/GitHub';
import WhatshotIcon from '@material-ui/icons/Whatshot';

function Project() {
    return (<>
        <div className="navbar">
            <div className="navbar__active">Projects</div>
            <div className="navbar__title">
            
            <Link to = "/Body">
            <a href="/resume">
                <div className="navbar__item" onClick={Body}>About</div></a>
            </Link>
            <Link to ="/Resume">
                <a href="/projects">
                <div className="navbar__item" onClick={Resume}>Resume</div></a>
                </Link>
            </div>
        </div>

        <div className="projects__row">
        <a href="https://clone-2d6c5.web.app" target="_blank">
            <img src="updown.png" class="projects_icon"/>
        </a>
      

        <a href="https://ishai2s4.github.io/Persoality/" target="_blank">
            <img src="personality.png" class="projects_icon"/>
        </a>

        <a href="" target="_blank">
            <img src="parking.png" class="projects_icon"/>
        </a>
        </div>


        <p className="projects__row1" style={{marginLeft: "55px", marginTop:"-20px"}}>
        <span className="projects__row1_one">
        <GitHubIcon style={{ fill: '#00f260',marginBottom:"-5px" }}/>Demo
        </span>
        <span className="projects__row1_two" style={{marginLeft: "55px"}}>
        <GitHubIcon style={{ fill: '#00f260',marginBottom:"-5px" }}/>Your Personality
        </span>
        <span className="projects__row1_three" style={{marginLeft: "-45px"}}>
        <GitHubIcon style={{ fill: '#00f260',marginBottom:"-5px" }}/>Parking
        </span>
        {/* </div>
        <div className="projectCard col-md-6 col-lg-4 my-2 bg-color:pink">
             */}
        </p>

        <div className="projects__row2"style={{marginTop: "-20px"}}>
        <a href="https://clone-2d6c5.web.app" target="_blank">
            <img src="amazon.png" class="projects_icon"/>
        </a>
      

        <a href="https://spotify-clone-8259d.web.app/" target="_blank">
            <img src="spotify.png" class="projects_icon"/>
        </a>
        </div>
        <div className="personality_row3" style={{marginLeft: "-30px", marginTop: "-30px"}}>
        <span className="projects__row1_four" style={{marginLeft: "150px"}}>
        <WhatshotIcon style={{ fill: 'yellow', marginBottom:'-5px' }}/>Amazon-clone</span>
        <span className="projects__row1_five" style={{marginLeft: "220px"}}>
        <WhatshotIcon style={{ fill: 'yellow', marginBottom:'-5px' }}/>Spotify-clone
        </span>
        </div>

        </>

    )
}

export default Project
