import React from 'react';
import './Navbar.css';
import Resume from './Resume';
import Projects from './ProBody';
import {Link} from 'react-router-dom';

function Navbar() {
    return (
        <div className="navbar">
            <div className="navbar__active">About</div>
            <div className="navbar__title">
            <Link to="/Resume">
                <a href="/resume">
                <div className="navbar__item" onClick={Resume}>Resume</div></a>
            </Link>
            <Link to="/projects">
                <a href="/projects">
                <div className="navbar__item" onClick={Projects}>Projects</div></a>
            </Link>
            </div>
        </div>
    )
}

export default Navbar
